 padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n// Adjust content container width for larger screens\nbody {\n  max-width: 2400px; // Increased to match gallery container\n  margin: 25px auto;\n  padding: 0 30px; // Increased padding for larger screens\n\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) {\n    max-width: 2400px;\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 1400px;\n  }\n\n  @media (min-width: 769px) and (max-width: 1500px) {\n    max-width: 1400px;\n  }\n\n  @media (max-width: 764px) {\n    max-width: 700px;\n    padding: 0 15px;\n  }\n}\n\n.banner-image {\n  object-fit: contain;\n  width: 100%;\n  image-rendering: pixelated;\n  border-radius: $border-radius $border-radius 0 0;\n}\n\n.image-placeholder {\n  display: inline-block;\n  //margin-bottom: 420px;\n  margin: calc(100vh * 0.3552 + 87.6051px) 0;\n  //transition: .3s ease-in-out;\n\n  @media (max-width: 768px) {\n    //flex: 0 0 100%;\n    //max-width: none;\n    margin: 200px;\n  }\n\n}\n\n// Content sections\n.content {\n  background: $card-bg;\n  padding: 25px 25px 25px 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n}\n\n#fullpage {\n  display: none;\n  position: absolute;\n  z-index: 9999;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-size: cover;\n  background-repeat: no-repeat no-repeat;\n  background-position: center center;\n  background-color: black;\n  background-blend-mode: normal;\n  image-rendering: pixelated;\n}\n\n\n// description styles\np.description {\n  font-size: 3.4rem;\n  font-family: $description-font;\n  border-radius: $border-radius;\n  line-height: 0.8;\n}\n\n// Gallery styles\n.gallery-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n.artwork-grid {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    .artwork-item {\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    .artwork-item {\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n  }\n\n  @media (min-width: 764px) and (max-width: 1500px) { // 2 items per row\n    .artwork-item {\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n  }\n}\n\n.artwork-item {\n  background: color.adjust($card-bg, $alpha: 0.9);\n  border-radius: $border-radius;\n  box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n  overflow: hidden;\n  margin-bottom: 20px;\n  position: relative;\n  display: inline-block;\n\n  img {\n    object-fit: contain;\n    width: 100%;\n    image-rendering: pixelated;\n    border-radius: $border-radius $border-radius 0 0;\n    display: block;\n    //@media (max-width: 768px) {\n    //  height: 300px;\n    //}\n  }\n\n  // Default (mobile) styling\n  @media (max-width: 768px) {\n    flex: 0 0 100%;\n    max-width: none;\n  }\n\n  &:hover {\n    transform: translateY(-5px);\n    box-shadow: 0 6px 16px color.adjust($card-bg, $alpha: -0.4);\n    background: color.adjust($card-bg, $alpha: 1);\n\n    .artwork-item {\n      display: none;\n    }\n  }\n}\n\n.artwork-info {\n  position: relative;\n  background-color: #3c3f41;\n  color: #fff;\n  padding: 10px 20px;\n  font-size: 20px;\n  text-align: center;\n\n  .artwork-title {\n    font-size: 2.4rem;\n    margin: 0 0 15px 0;\n    color: $text-light;\n  }\n\n  .artwork-description {\n    font-size: 1.4rem;\n    color: color.adjust($text-light, $alpha: -0.15);\n    line-height: 1.4;\n  }\n}\n\n.icon-image {\n  height: 100px;\n  width: 100%;\n  object-fit: fill;\n  border-radius: $border-radius $border-radius 0 0;\n  filter: invert(28%) sepia(79%) saturate(2476%) hue-rotate(56deg) brightness(118%) contrast(119%);\n}\n\n.icon-item {\n  .icon-info {\n    padding: 10px;\n    margin-right: 10px;\n\n    .icon-title {\n      font-size: 2.4rem;\n      margin: 0 0 10px 0;\n      color: $text-light;\n    }\n\n    .icon-description {\n      font-size: 2.4rem;\n      color: color.adjust($text-light, $alpha: -0.15);\n      line-height: 1.2;\n      text-align: center;\n    }\n  }\n}\n\n\n// Blockquote styles\nblockquote {\n  border-left: 2px solid color.adjust($accent-color, $saturation: 10%);\n  margin: 1.6em 0;\n  padding: 1.5em;\n  font-size: 1.8rem;\n  background: color.adjust($card-bg, $alpha: -0.4);\n  border-radius: $border-radius;\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n}\n\n\n"],"sourceRoot":""}]);
